.selectsteel {
    box-sizing: border-box;
    width: 100%;
    max-width: 800px;
    padding: 30px 50px;
    margin: 20px 30px;
    border-radius: 6px;
    background: #fff;
}

.selectsteel-rating {
    box-sizing: border-box;
    width: 100%;
    max-width: 800px;
    padding: 30px 50px;
    margin: 20px 30px;
    border-radius: 6px;
    background: #e8e8e8;
}

.selectsteel-itemfield {
    width: 100%;
    color: #7b7b7b;
    font-size: 16px;
}

.selectsteel-itemfield input {
    box-sizing: border-box;
    width: 100%;
    height: 50px;
    border-radius: 4px;
    padding-left: 15px;
    border: 1px solid #c3c3c3;
    outline: none;
    color: #7b7b7b;
    font-family: Poppins;
    font-size: 14px;
}

.selectsteel-priority {
    display: flex;
    gap: 40px;
}



.selectsteel-itemfield div {
    display: flex;
    width: 25%;
}



.selectsteel-itemfield div img {
    margin-left: -30px;
}

.selectsteel-selector {
    padding: 10px 10px;
    width: 200px;
    height: 50px;
    font-size: 14px;
    color: #7b7b7b;
    border: 1px solid #7b7b7b8d;
    border-radius: 4px;
}

.selectsteel-btn {
    margin-top: 40px;
    margin-left: 70px;
    width: 160px;
    height: 50px;
    border-radius: 6px;
    background: #6079ff;
    border: none;
    cursor: pointer;
    color: #FFF;
    font-size: 16px;
    font-weight: 500;
}

@media(max-width:800px) {
    .addproduct {
        width: auto;
        padding: 30px;
        margin: 20px 20px;
    }
}